body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
}
body {
  background-color: black;
}

#header {
  color: white;
  font-family: 'Faster One', cursive;
  font-size: 200px;
  text-align: center;
  text-shadow: 4px 4px gray;
}

#options {
  color: white;
  font-size: 10px;
  text-shadow: 2px 2px gray;
}

#startbtn {
  margin-top: 10px;
  width: 70px;
  height: 30px;
}

#menubtn {
  margin-top: 15px;
  margin-bottom: 15px;
  width: 100px;
  height: 30px;
}

#menubtnwrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

#canvas-wrapper {
  border: 0;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

#gamecanvas {
  width: 500px;
  height: 500px;
  border: 0;
  margin: 0;
  padding: 0;
}

#turn-info {
  margin-top: 25px;
  color: white;
  text-align: center;
  font-size: 45px;
  font-family: 'Audiowide', cursive;
}

#difficulty-info {
  margin-top: 20px;
  text-align: center;
  font-size: 30px;
  font-family: 'Notable', sans-serif;
}

#turn-info span {
  color: gray;
  font-family: 'Audiowide', cursive;
}

#gridsize,#aidifficulty {
  margin-bottom: 5px;
}
